import * as React from 'react';
import { createContext, useCallback, useContext, useEffect, useState } from 'react';
import fetchCommands from '../helpers/fetchCommands';
import doInvokeCommand from '../helpers/doInvokeCommand';
export const CommandsContext = createContext({});
export const useCommands = () => useContext(CommandsContext);
const ConsoleStyle = {
    base: ['color: #fff', 'background-color: #00adee', 'font-weight: bold', 'padding: 2px 4px', 'border-radius: 2px'],
    error: ['color: #fff', 'background-color: red'],
    success: ['color: #fff', 'background-color: #00a338'],
    text: ['color:#fff'],
};
const logToConsole = (type = 'log', text, ...args) => {
    let finalStyle = ConsoleStyle.base.join(';') + ';';
    if (ConsoleStyle[type]) {
        finalStyle += ConsoleStyle[type].join(';');
    }
    console[type](`%c[Neos.Terminal]%c ${text}:`, finalStyle, ConsoleStyle.text.join(';'), ...args);
};
export const CommandsProvider = ({ invokeCommandEndPoint, getCommandsEndPoint, children, documentNode, focusedNode, siteNode, i18nRegistry, handleServerFeedback, }) => {
    const [commands, setCommands] = useState({});
    useEffect(() => {
        fetchCommands(getCommandsEndPoint).then(({ result }) => setCommands(result));
    }, [getCommandsEndPoint, setCommands]);
    const translate = useCallback((id, fallback = '', params = [], packageKey = 'Shel.Neos.Terminal', sourceName = 'Main') => {
        return i18nRegistry.translate(id, fallback, params, packageKey, sourceName);
    }, []);
    const invokeCommand = useCallback((commandName, args) => {
        const command = commands[commandName];
        if (!command)
            throw Error(translate('command.doesNotExist', `The command {commandName} does not exist!`, { commandName }));
        return doInvokeCommand(invokeCommandEndPoint, commandName, args, siteNode, focusedNode, documentNode).then(({ success, result, uiFeedback }) => {
            let parsedResult = result;
            let textResult = result;
            // Try to prettify json results
            try {
                parsedResult = JSON.parse(result);
                if (typeof parsedResult !== 'string') {
                    textResult = JSON.stringify(parsedResult, null, 2);
                }
                else {
                    textResult = parsedResult;
                }
            }
            catch (e) {
                // Treat result as simple string
            }
            logToConsole(success ? 'log' : 'error', translate('command.output', `"{commandName} {argument}":`, {
                commandName,
                argument: args.join(' '),
            }), parsedResult);
            // Forward server feedback to the Neos UI
            if (uiFeedback) {
                handleServerFeedback(uiFeedback);
            }
            return textResult;
        });
    }, [commands, siteNode, documentNode, focusedNode]);
    return (React.createElement(CommandsContext.Provider, { value: { invokeCommand, commands, translate } }, children));
};
